.cartpage{
    width: 70%;
    margin: auto;
}

.cart-items{
    width: 100%;
    display: flex;
    align-items: center;
    margin: 8px 0px;
    justify-content: space-around;
    box-shadow: 6px 6px 6px #80808094;
    border-radius: 10px;
    height: 183px;
    text-align: center;
    min-width: 856px;
}

.img-cart-item{
    min-width:275px;
}

.order-price{
    display: flex;
    justify-content: flex-end;
    margin: 8px 0px;
    color: green;
    font-weight: 500;
    align-items: center;
}
.cart-title{
    min-width: 300px;
}

.cart-price{
    min-width: 45px;
}

.cart-quantity{
    min-width: 45px;
}

.cart-btn{
    min-width: 125px;
}
.rmcart{
    background: rgba(0,0,0,.23);
    border: none;
    border-radius: 3px;
    padding: 8px;
}